<template>
  <!-- 有子菜单 -->
  <el-submenu
    v-if="menu.children && menu.children.length > 0"
    :index="menu.path"
  >
    <template slot="title">
      <i :class="menu.icon || 'el-icon-menu'"></i>
      <span slot="title">{{ menu.title }}</span>
    </template>
    <menu-item
      v-for="child in menu.children"
      :key="child.id"
      :menu="child"
    />
  </el-submenu>

  <!-- 无子菜单 -->
  <el-menu-item
    v-else
    :index="menu.path"
    :route="{ path: menu.path }"
  >
    <i :class="menu.icon || 'el-icon-menu'"></i>
    <span slot="title">{{ menu.title }}</span>
  </el-menu-item>
</template>

<script>
export default {
  name: 'MenuItem',
  props: {
    menu: {
      type: Object,
      required: true
    }
  }
}
</script>



