<template>
  <div class="app-container">
    <!-- 顶部导航模式 -->
    <template v-if="navPosition === 'top'">
      <el-menu
        :default-active="activeMenu"
        mode="horizontal"
        :router="true"
        background-color="#304156"
        text-color="#bfcbd9"
        active-text-color="#409EFF"
      >
        <div class="logo-container">
          <img src="@/assets/logo.png" class="logo">
          <span class="system-name">管理系统</span>
        </div>

        <template v-for="menu in menuTree">
          <menu-item :menu="menu" :key="menu.id" :is-top="true" />
        </template>

        <div class="user-info">
          <el-dropdown trigger="click">
            <span class="el-dropdown-link">
              <i class="el-icon-user"></i>
              {{ user.username }}
              <i class="el-icon-arrow-down"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item @click.native="logout">退出登录</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </el-menu>

      <div class="main-content">
        <router-view></router-view>
      </div>
    </template>

    <!-- 侧边导航模式 -->
    <template v-else>
      <el-container style="height: 100vh;">
        <el-aside :width="isCollapse ? '64px' : '200px'">
          <div class="logo-container">
            <img src="@/assets/logo.png" class="logo">
            <span v-show="!isCollapse" class="system-name">管理系统</span>
          </div>

          <el-menu
            :default-active="activeMenu"
            :collapse="isCollapse"
            :collapse-transition="false"
            :router="true"
            background-color="#304156"
            text-color="#bfcbd9"
            active-text-color="#409EFF"
          >
            <template v-for="menu in menuTree">
              <menu-item :menu="menu" :key="menu.id" />
            </template>
          </el-menu>
        </el-aside>

        <el-container>
          <el-header height="50px">
            <div class="header-content">
              <div class="collapse-btn" @click="toggleCollapse">
                <i :class="isCollapse ? 'el-icon-s-unfold' : 'el-icon-s-fold'"></i>
              </div>

              <div class="nav-position">
                <el-radio-group v-model="navPosition" size="small">
                  <el-radio-button label="left">侧边导航</el-radio-button>
                  <el-radio-button label="top">顶部导航</el-radio-button>
                </el-radio-group>
              </div>

              <div class="user-info">
                <el-dropdown trigger="click">
                  <span class="el-dropdown-link">
                    <i class="el-icon-user"></i>
                    {{ user.username }}
                    <i class="el-icon-arrow-down"></i>
                  </span>
                  <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item @click.native="logout">退出登录</el-dropdown-item>
                  </el-dropdown-menu>
                </el-dropdown>
              </div>
            </div>
          </el-header>

          <el-main>
            <router-view></router-view>
          </el-main>
        </el-container>
      </el-container>
    </template>
  </div>
</template>

<script>
import MenuItem from '@/components/MenuItem.vue'
import { mapState, mapGetters, mapMutations } from 'vuex'

export default {
  components: { MenuItem },
  computed: {
    ...mapState(['isCollapse', 'navPosition', 'user']),
    ...mapGetters(['menuTree']),
    activeMenu() {
      return this.$route.path
    }
  },
  methods: {
    ...mapMutations(['toggleCollapse', 'setNavPosition']),
    logout() {
      this.$store.dispatch('logout').then(() => {
        this.$router.push('/login')
      })
    }
  }
}
</script>

<style scoped>
.app-container {
  height: 100vh;
  overflow: hidden;
}

.logo-container {
  display: flex;
  align-items: center;
  height: 60px;
  padding: 0 20px;
  background: #2b2f3a;
  color: #fff;
  overflow: hidden;
}

.logo {
  width: 32px;
  height: 32px;
  margin-right: 12px;
}

.system-name {
  font-size: 18px;
  font-weight: bold;
  white-space: nowrap;
}

.user-info {
  position: absolute;
  right: 20px;
  color: #fff;
  cursor: pointer;
}

.el-dropdown-link {
  color: #fff;
}

.header-content {
  display: flex;
  align-items: center;
  height: 100%;
  padding: 0 20px;
  background: #fff;
  box-shadow: 0 1px 4px rgba(0, 21, 41, 0.08);
}

.collapse-btn {
  margin-right: 20px;
  font-size: 20px;
  cursor: pointer;
}

.nav-position {
  flex: 1;
}

.main-content {
  padding: 20px;
}
</style>



